import React, { useState } from 'react';
import './Navbar.css';

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className='pos'>
    <nav className="navbar">
      <div className="navbar-brand"><span className='colo'>H</span><span className='colo2'>imanshu </span><span className='colo'>M</span><span className='colo2'>ehta</span></div>
      <div className="menu-toggle" onClick={toggleMenu}>
        ☰
      </div>
      <div className='contain'>
      <div className={`navbar-links ${isOpen ? 'open' : ''}`}>
        <a href="#home">Home</a>
        <a href="#skills">Skills</a>
        <a href="#projects">Projects</a>
        <a href="#experience">Experience</a>
        <a href="#contact">Contact</a>
        <span className="separator">|</span>
        <a href="https://github.com/Himanshu0-1-0" target="_blank" rel="noopener noreferrer" className="social-icon">
            <i className="fab fa-github"></i>
          </a>
          <a href="https://www.linkedin.com/in/himanshu-mehta-804a36248/" target="_blank" rel="noopener noreferrer" className="social-icon">
            <i className="fab fa-linkedin-in"></i>
          </a>
          <a href="https://leetcode.com/u/himanshu0_1_0/" target="_blank" rel="noopener noreferrer" className="social-icon">
            <i className="fas fa-code"></i>
          </a> 
      </div>
      </div>
      
    </nav>
    </div>
  );
};

export default Navbar;
