import "./Home.css"
import TypeIt from "typeit-react";
export default function Home() {
  return (
    <>
      <div className="body-section" id="home">
      <div className="photo-container">
        <img src="Photo.jpg" alt="Himanshu Mehta" className="photo" />
      </div>
      <div className="intro-container">
        <h2 className="intro-line">-Hello</h2>
        <h1 className="intro-line">I'm <span>H</span>imanshu <span>M</span>ehta</h1>
        <p className="intro-line">A  <TypeIt className="intro-line typed"
                options={{
                    loop:true
                }} 
                getBeforeInit={(instance) => {
                  instance.type("Full Stack Web Developer...").pause(800).delete(27).pause(800).type("Programmer...").pause(800).delete(13).pause(800).type("Problem Solver...").pause(750);
              
                  // Remember to return it!
                  return instance;
                }}
            /></p>
        <p className="intro-paragraph">
        Welcome to my Portfolio. As a final year student pursuing Electronics and Communication Engineering from Punjab Engineering college, I have developed a great interest in Web Development and Problem Solving. I am proficient in MERN stack and currently engaged in Competitive programming and learning about Next.js and Machine learning. This website serves as a overview about me and my skills. 
       </p>
      </div>
    </div>
    </>
  )
}
